import { Input, Space, Card, List, Avatar, Row, Col } from "antd";
import { Typography } from 'antd';
import { useEffect, useState } from "react";

const { Title } = Typography;
const { Search } = Input;




export default function Chat() {
  const [ws, setWS] = useState(null)
  const [user, setUser] = useState(window.sessionStorage.getItem("username"))
  const [useList, setUserList] = useState([])
  const [msgList, setMsgList] = useState([])


  const onSetUser = (value) => {
    console.log("on set user:", value)
    window.sessionStorage.setItem("username", value)
    setUser(value)
    ws.send(JSON.stringify({
      type: "join_lobby",
      user: user
    }))
  }

  const onSendMsg = (value) => {
    ws.send(JSON.stringify({
        type: "chat_msg",
        user: "test",
        msg: value
    }))
  }

  useEffect(() => {
    let websocket = new WebSocket("ws://localhost:8000/ws/chat")
    // send
    // onmessage


    websocket.onopen = (event) => {
      console.log("onopen: ", event);
      if (user) {
        console.log("join_lobby on open")
        websocket.send(JSON.stringify({
          type: "join_lobby",
          user: user,
        }))
      }
    };

    websocket.onmessage = (ev) => {
      console.log("onmessage:", ev);

      const data = JSON.parse(ev.data);
      console.log("receive:", data);
      
      switch (data.type) {
          case "chat_user":
              console.log("chat_login:", data.msg)
              setUserList(data.msg)
              break
          case "chat_text":
              setMsgList(data.msg)
              break
          default:
              break
      }
    };

    websocket.onerror = (event) => {
      console.log("onerror: ", event);
    };
      
    // window.onunload = () => {
    //   console.log("before window close")
    //   websocket.send(JSON.stringify({
    //     type: 'chat_disconnet',
    //     msg: 'chat disconnet'
    //   }))
    //   // websocket.close()
    // }

    setWS(websocket)
  }, []);

  if (!user) {
    return (
      <Search
        placeholder="请输入用户名"
        allowClear
        enterButton="确认"
        size="large"
        onSearch={onSetUser}
      />
    )
  }

  return (
    <>
      <Row>


        <Col span={4}>
          <Title level={2}> 在线人数: { useList.length } </Title>

          <List
            itemLayout="horizontal"
            dataSource={useList}
            renderItem={(item) => (
              <List.Item>
                <List.Item.Meta
                  avatar={<Avatar src="https://joeschmoe.io/api/v1/random" />}
                  title={<a href="https://ant.design">{item.client}</a>}
                  description={item.ua}
                />
              </List.Item>
            )}
          />
        </Col>

        <Col>
          <Space direction="vertical">

            <Search
              placeholder={user}
              allowClear
              enterButton="确认"
              size="large"
              onSearch={onSetUser}
            />

            <Card
              title="信息"
              bodyStyle={{ height: "60vh", overflow: "auto"}}
            >
                {msgList.map((value, index) => {
                  return <Card key={index}>
                      <p> {value.msg} </p>
                  </Card>
                })}   
            </Card>

            <Search
              placeholder="input text"
              allowClear
              enterButton="发送"
              size="large"
              onSearch={onSendMsg}
            />
          </Space>
        </Col>
      </Row>
    </>
  );
}
